<script>
    import * as API from '@/api/creditManagement'
    export default {
        name: 'baseform',
        data: function(){
            return {
                tableData: [{
                    name: '21'
                }],
                tableDataBill: [{
                    name: '21'
                }],
                collapseActive: ['1'],
                creditType: 1, // 1助贷，2自贷
                form: {
                    name: '阿萨德'
                }
            }
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            
            getFormData(){
                let _query = this.$route.query
                this.creditType = _query.credit_type
                if(_query.page_type){
                    API.creditManagementBlackView({id: _query.id, credit_type: _query.credit_type}).then(res => {
                        Object.assign(this.form,res.data)
                        this.$forceUpdate()
                    })
                }else{
                   API.creditHelpManagementsetView({id: _query.id, credit_type: _query.credit_type}).then(res => {
                        Object.assign(this.form,res.data)
                        this.$forceUpdate()
                    })
                }
                
            }
        }
    }
</script>

